import React, { useContext } from "react";
import { QueriesContext } from "../../../QueriesContext";

const SkeletonCards = () => {
  const { size } = useContext(QueriesContext);

  return (
    <div className=" grid gap-2 animate-pulse sm:hidden w-full">
      {Array(size).fill(1, 0, size).map((e, count) => (
        <div key={count} className="h-20 w-full  bg-indigo-100 rounded-2xl p-2 relative border-2 shadow-md">
          <div className=" h-full w-20 bg-indigo-50 animate-pulse rounded-md" />
        </div>
      ))}
    </div>
  );
};

export default SkeletonCards;
